import React, { useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { ThreeDots } from 'react-loader-spinner';

import bg from '../../Assets/Images/planning_15679287.png';
import Style from './Register.module.css'; 

function Register() {
    const [errMsg, setErrMsg] = useState('');
    const [loading, setLoading] = useState(false);
    const navigate = useNavigate();

    const emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/i;
    const phoneRegex = /^((\+[1-9]{1,4}[ \-]*)|(\([0-9]{2,3}\)[ \-]*)|([0-9]{2,4})[ \-]*)*?[0-9]{3,4}?[ \-]*[0-9]{3,4}?$/;
    const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
    const ageRegex = /^\S[0-9]{0,3}$/;

    const validationSchema = Yup.object({
        name: Yup.string().min(6, 'Your name must be at least 6 characters long.').max(50, 'Your name must be 50 characters at most.').required('Your name is required.'),
        email: Yup.string().email('Please enter a valid email address.').matches(emailRegex, 'Please enter a valid email address.').required('Your email is required.'),
        phone: Yup.string().matches(phoneRegex, 'Please enter a valid mobile number.').required('Your mobile number is required.'),
        password: Yup.string().min(8, 'Your password must be at least 8 characters long.').matches(passwordRegex, 'Please choose a more secure password.').required('Enter a combination of at least 8 characters, letters, and punctuation marks.'),
        age: Yup.string().required('Your Age is required').matches(ageRegex, 'Please enter a valid age.'),
    });

    const formik = useFormik({
        initialValues: {
            name: '',
            email: '',
            password: '',
            age: '',
            phone: '',
        },
        validationSchema,
        onSubmit: async (values) => {
            setLoading(true);
            try {
                const response = await axios.post('https://note-sigma-black.vercel.app/api/v1/users/signUp', values);
                console.log(response);
                navigate('/signin');
            } catch (error) {
                setLoading(false);
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.message);
                }
            }
        },
    });

    return (
        <div className="container ">
            <div className="row justify-content-center align-items-center" style={{ minHeight: '80vh' }}>
                <div className="col-md-4">
                    <div className="card shadow-lg border-0" style={{ borderRadius: '12px', overflow: 'hidden' }}>
                        <div className="card-header text-center text-white" style={{ backgroundColor: '#5e35b1' }}>
                            <h3>Create an Account</h3>
                        </div>
                        <div className="card-body">
                            <form onSubmit={formik.handleSubmit}>
                                {errMsg && <div className="alert alert-danger text-center">{errMsg}</div>}

                                {['name', 'email', 'password', 'age', 'phone'].map((field, idx) => (
                                    <div key={idx} className="form-group my-3">
                                        <label htmlFor={field} className="fs-5 text-muted text-capitalize">{field}</label>
                                        <input
                                            type={field === 'password' ? 'password' : 'text'}
                                            className={`form-control border-0 shadow-sm ${formik.touched[field] && formik.errors[field] ? 'is-invalid' : ''}`}
                                            style={{ borderRadius: '8px' }}
                                            id={field}
                                            name={field}
                                            value={formik.values[field]}
                                            onBlur={formik.handleBlur}
                                            onChange={formik.handleChange}
                                        />
                                        {formik.touched[field] && formik.errors[field] && (
                                            <div className="invalid-feedback">{formik.errors[field]}</div>
                                        )}
                                    </div>
                                ))}

                                <div className="text-center mt-4">
                                    {loading ? (
                                        <button
                                            className="btn btn-lg w-100 text-white"
                                            style={{ backgroundColor: '#5e35b1', display: 'flex', justifyContent: 'center', alignItems: 'center' }}
                                            disabled
                                        >
                                            <ThreeDots
                                                visible={true}
                                                height="30"
                                                width="40"
                                                color="#fff"
                                                ariaLabel="three-dots-loading"
                                            />
                                        </button>
                                    ) : (
                                        <button
                                            type="submit"
                                            className="btn btn-lg w-100 text-white"
                                            disabled={!(formik.isValid && formik.dirty)}
                                            style={{ backgroundColor: '#5e35b1', borderRadius: '8px' }}
                                        >
                                            Register
                                        </button>
                                    )}
                                    <p className="text-center mt-3 text-muted fs-5">
                                        Already have an account? <Link className="text-primary fs-5" to="/signin">Login</Link>
                                    </p>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Register;
