import React, { useContext, useEffect, useState } from 'react';
import Style from './Home.module.css';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import axios from 'axios';
import Note from '../Note/Note';
import NoteContext from '../../Contexts/NoteContext';
import Spinner from '../Spinner/Spinner';

function Home() {
  let { getUserNote } = useContext(NoteContext);
  const [errMsg, setErrMsg] = useState('');
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const [notes, setNotes] = useState([]);
  const headers = { token: "3b8ny__" + localStorage.getItem("userToken") };
  const [loading, setLoading] = useState(false);
  async function addNote(values) {
    setLoading(true);
    try {
      let { data } = await axios.post('https://note-sigma-black.vercel.app/api/v1/notes', values, { headers });
      console.log('Response Data:', data);
      if (data.msg === 'done') {
        setLoading(false)
        getNotes();
        setNotes(data.notes);
        setErrMsg('');
        handleClose();
        formik.resetForm();
      }
    } catch (error) {
      setLoading(false)
      if (error.response && error.response.data) {
        setErrMsg(error.response.data.message);
        console.error('Error Response:', error.response);
      } else {
        console.error('Error:', error);
      }
    }
  }

  const validationSchema = Yup.object({
    title: Yup.string().required('Title is required'),
    content: Yup.string()
      .required('Content is required')
      .min(20, 'Content must be at least 20 characters long.')
      .max(60, 'Content must be no more than 60 characters long.')
  });

  let formik = useFormik({
    initialValues: {
      title: '',
      content: ''
    },
    validationSchema,
    onSubmit: addNote
  });

  useEffect(() => {
    getNotes();
  }, []);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  async function getNotes() {
    try {
      let { data } = await getUserNote();
      console.log(data);
      if (data.msg === 'done') {
        setNotes(data.notes);
      }
    } catch (error) {
      if (error.response && error.response.data) {
        setErrMsg(error.response.data.message);
        console.error('Error Response:', error.response);
      } else {
        console.error('Error:', error);
      }
    }
  }

  return (
    <>{loading?<Spinner />:
      <><div className='container'>
      <h2 className='py-3 mainColor' style={{ borderBottom: '3px solid #874CCC' }}>
        <i className="far fa-folder-open fs-4 ms-2"></i> My Notes
      </h2>
      <Button className='fs-5 p-2' style={{ backgroundColor: '#874CCC' }} onClick={handleShow}>
        Add Note
      </Button>
      <div className="row justify-content-center">
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Add Note</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <form onSubmit={formik.handleSubmit}>
              {errMsg && <div className="alert alert-danger">{errMsg}</div>}
              <div className="mb-3">
                <label htmlFor='title' className='fs-5'>Title</label>
                <input
                  type="text"
                  className={`form-control form-control-lg ${formik.touched.title && formik.errors.title ? 'is-invalid' : ''}`}
                  id="title"
                  name='title'
                  value={formik.values.title}
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                />
                {formik.touched.title && formik.errors.title && <div className='invalid-feedback'>{formik.errors.title}</div>}
              </div>
              <div className="mb-3">
                <label htmlFor='content' className='fs-5'>Content</label>
                <textarea
                  className={`form-control form-control-lg ${formik.touched.content && formik.errors.content ? 'is-invalid' : ''}`}
                  id="content"
                  name='content'
                  value={formik.values.content}
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                ></textarea>
                {formik.touched.content && formik.errors.content && <div className='invalid-feedback'>{formik.errors.content}</div>}
              </div>
              <div className="d-flex justify-content-end">
                <Button variant="secondary" onClick={handleClose}>
                  Cancel
                </Button>
                <Button type='submit' className='btn btn-primary ms-2'>
                  Add Note
                </Button>
              </div>
            </form>
          </Modal.Body>
        </Modal>
      </div>
    </div>
    <div className='container'>
      <div className='row mx-auto gy-1 note-row'>
        {notes? notes.map((note, index) => (
          <Note note={note} key={index} index={index + 1} func={getNotes} />
        )) : ''}
      </div>
    </div>   </>
    }
  
    </>
  );
}

export default Home;
