import React, { useContext, useEffect, useState } from 'react';
import Style from './Note.module.css';
import Card from 'react-bootstrap/Card';
import NoteContext from '../../Contexts/NoteContext';
import * as Yup from 'yup';
import { useFormik } from 'formik';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Spinner from '../Spinner/Spinner';


function Note({note , func ,index}) {
    let {deleteNote ,updateNote} = useContext(NoteContext);
    const [errMsg, setErrMsg] = useState('');
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow1 = () => setShow(true);
    const [notes, setNotes] = useState([]);
    const [loading, setLoading] = useState(false);

  
    const validationSchema = Yup.object({
      title: Yup.string().required('Title is required'),
      content: Yup.string()
        .required('Content is required')
        .min(10, 'Content must be at least 20 characters long.')
        .max(60, 'Content must be no more than 60 characters long.')
    });
  
    let formik = useFormik({
      initialValues: {
        title: note.title,
        content: note.content
      },
      validationSchema,
      onSubmit: async (values) => {
        setLoading(true)
        try {
          let { data } = await updateNote(note._id , values);
          console.log('Response Data:', data);
              if(data.msg === 'done'){
                setLoading(false)
                setNotes(data.notes);
                func()
                  setErrMsg('');
                  handleClose();
                  formik.resetForm();
              }
        
        } catch (error) {
          setLoading(false)
          if (error.response && error.response.data) {
            setErrMsg(error.response.data.message);
            console.error('Error Response:', error.response);
          } else {
            console.error('Error:', error);
          }
        }
      }
    });



async function removeNote(noteId){
        try {
        let {data} = await deleteNote(noteId);
        console.log('Response Data:', data);
        if(data.msg === 'done'){
            func();
            setErrMsg('');
        }
  }
  catch(error) {
    if (error.response && error.response.data) {
      setErrMsg(error.response.data.message);
      console.error('Error Response:', error.response);
    } else {
      console.error('Error:', error);
    }}
}

     
    return <>
   {loading?<Spinner />:
   <> <div className='col-sm-6 col-md-4 '>
   <Card className='mt-2'>
    <Card.Header className='py-3' style={{backgroundColor:'#874CCC'}}>
       <Card.Title className='text-end fs-3 text-white fw-bold'>0{index}</Card.Title>
    </Card.Header>
    <Card.Body >
    <Card.Title className='text-center fs-4'  style={{color:'#6E5A8F'}}>{note.title}</Card.Title>
      <Card.Text className="text-muted fs-5 mb-0">
       {note.content}
      </Card.Text>
    <div className="row justify-content-center ">
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Add Note</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form onSubmit={formik.handleSubmit}>
            {errMsg && <div className="alert alert-danger">{errMsg}</div>}
            <div className="mb-3">
              <label htmlFor='title' className='fs-5'>Title</label>
              <input
                type="text"
                className={`form-control form-control-lg ${formik.touched.title && formik.errors.title ? 'is-invalid' : ''}`}
                id="title"
                name='title'
                value={formik.values.title}
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
              />
              {formik.touched.title && formik.errors.title && <div className='invalid-feedback'>{formik.errors.title}</div>}
            </div>
            <div className="mb-3">
              <label htmlFor='content' className='fs-5'>Content</label>
              <textarea
                type="text"
                className={`form-control form-control-lg ${formik.touched.content && formik.errors.content ? 'is-invalid' : ''}`}
                id="content"
                name='content'
                value={formik.values.content}
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
              ></textarea>
              {formik.touched.content && formik.errors.content && <div className='invalid-feedback'>{formik.errors.content}</div>}
            </div>
            <div className="d-flex justify-content-end">
              <Button variant="secondary" onClick={handleClose}>
                Cancel
              </Button>
              <Button type='submit' className='btn btn-primary ms-2'>
                Update Note
              </Button>
            </div>
          </form>
        </Modal.Body>
      </Modal>
    </div>
    </Card.Body>
    <Card.Footer>
    <div className='d-flex justify-content-center'>
          <Button className='btn btn-danger' onClick={()=>{removeNote(note._id)}}>Delete Note</Button>
          <Button className='btn btn-primary ms-3' onClick={handleShow1}>Update Note</Button>
       </div>
    </Card.Footer>
  </Card>
   </div></>
   }
    
    </>;
}

export default Note;