import React, { useContext, useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import bg from '../../Assets/Images/wel.jpg';
import Style from './Login.module.css';
import { Link, useNavigate } from 'react-router-dom';
import { ThreeDots } from 'react-loader-spinner';
import AuthContext from '../../Contexts/AuthContext';

function Login() {
    const [errMsg, setErrMsg] = useState('');
    const [loading, setLoading] = useState(false);
    const { token, setToken } = useContext(AuthContext);
    const navigate = useNavigate();

    const emailRegex = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/i;
    const passwordRegex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;

    const validationSchema = Yup.object({
        email: Yup.string()
            .email('Please enter a valid email address.')
            .matches(emailRegex, 'Please enter a valid email address.')
            .required('Your email is required.'),
        password: Yup.string()
            .min(8, 'Your password must be at least 8 characters long.')
            .matches(passwordRegex, 'Please choose a more secure password.')
            .required('Enter a combination of at least 8 characters, letters, and punctuation marks.'),
    });

    const formik = useFormik({
        initialValues: { email: '', password: '' },
        validationSchema,
        onSubmit: async (values) => {
            setLoading(true);
            setErrMsg(''); // Reset error message before new submission
            try {
                const { data } = await axios.post('https://note-sigma-black.vercel.app/api/v1/users/signIn', values);
                if (data.msg === 'done') {
                    setLoading(false);
                    localStorage.setItem('userToken', data.token);
                    setToken(data.token);
                    navigate('/');
                }
            } catch (error) {
                setLoading(false);
                if (error.response && error.response.data) {
                    setErrMsg(error.response.data.message);
                } else {
                    setErrMsg('Something went wrong. Please try again.');
                }
            }
        },
    });

    return (
        <div className={`container ${Style.container}`}>
            <div className="row justify-content-center">
                <div className="col-md-8 col-lg-6">
                    <div className={`card ${Style.card}`}>
                        <div className="row no-gutters">
                            <div className="col-md-6 d-none d-md-block">
                                <img src={bg} height="100%" className="card-img" alt="Welcome" />
                            </div>
                            <div className="col-md-6">
                                <div className="card-header">
                                    <h3 className='text-white text-center'>Sign in</h3>
                                </div>
                                <div className="card-body">
                                    {errMsg && (
                                        <div className="alert alert-danger text-center" role="alert">
                                            {errMsg}
                                        </div>
                                    )}
                                    <form onSubmit={formik.handleSubmit}>
                                        <div className="form-group my-3">
                                            <label htmlFor="email" className="fs-5">Email address</label>
                                            <input
                                                type="email"
                                                className={`form-control ${formik.touched.email && formik.errors.email ? 'is-invalid' : ''}`}
                                                id="email"
                                                name="email"
                                                onBlur={formik.handleBlur}
                                                value={formik.values.email}
                                                onChange={formik.handleChange}
                                            />
                                            {formik.touched.email && formik.errors.email && <div className="invalid-feedback">{formik.errors.email}</div>}
                                        </div>

                                        <div className="form-group my-3">
                                            <label htmlFor="password" className="fs-5">Password</label>
                                            <input
                                                type="password"
                                                className={`form-control ${formik.touched.password && formik.errors.password ? 'is-invalid' : ''}`}
                                                id="password"
                                                name="password"
                                                value={formik.values.password}
                                                onBlur={formik.handleBlur}
                                                onChange={formik.handleChange}
                                            />
                                            {formik.touched.password && formik.errors.password && <div className="invalid-feedback">{formik.errors.password}</div>}
                                        </div>

                                        {loading ? (
                                            <button
                                                className={`btn btn-lg w-100 ${Style['btn-custom']}`}
                                                disabled
                                                style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}
                                            >
                                                <ThreeDots visible={true} height="30" width="40" color="#fff" ariaLabel="three-dots-loading" />
                                            </button>
                                        ) : (
                                            <>
                                                <button type="submit" className={`btn btn-lg w-100 text-white ${Style['btn-custom']}`}>
                                                    Sign in
                                                </button>
                                                <h5 className="text-center mt-3">
                                                    Don't have an account?{' '}
                                                    <Link className={`text-decoration-none ${Style['text-primary-custom']}`} to="/signup">
                                                        Create an account.
                                                    </Link>
                                                </h5>
                                            </>
                                        )}
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Login;
