import axios from 'axios';
import React, { createContext , useState } from 'react';

const NoteContext = createContext();

const headers = { token: "3b8ny__" + localStorage.getItem("userToken") };

function getUserNote(){
return axios.get('https://note-sigma-black.vercel.app/api/v1/notes' , {headers})
.then((response)=>{ return response})
.catch((error)=>{ return error})
}

function updateNote(noteId , values){
    return axios.put(`https://note-sigma-black.vercel.app/api/v1/notes/${noteId}`, values , {headers})
    .then((response)=>{ return response})
    .catch((error)=>{ return error})
}
function deleteNote(noteId){
    return axios.delete( `https://note-sigma-black.vercel.app/api/v1/notes/${noteId}` , {headers})
    .then((response)=>{ return response})
    .catch((error)=>{ return error})
}

export function NoteContextProvider(props) {    
    return <NoteContext.Provider value={{  getUserNote  ,updateNote ,deleteNote}}>
       {props.children}
    </NoteContext.Provider>;
}

export default NoteContext;