import React, { useContext, useState } from 'react';
import Style from './SideBar.module.css';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import AuthContext from '../../Contexts/AuthContext';

function SideBar() {
    let location = useLocation();
    const { setToken ,token} = useContext(AuthContext);
    const [isSidebarOpen, setSidebarOpen] = useState(true);
    let navigate = useNavigate();
   

    function logOut() {
        localStorage.removeItem('userToken');
        setToken(null);
        navigate('./signin');
    }

    // function toggleSidebar() {
    //     setSidebarOpen(!isSidebarOpen);
  
    // }
   
    return (
        <>
            {token ? (
                <div id="sidebar" className={`shadow d-flex flex-column flex-shrink-0 p-3 ${isSidebarOpen ? '' : Style.hidden}`} style={{ width: "280px" }}>
                    <nav>
                        {/* <div className="custom-menu">
                            <button type="button" id="sidebarCollapse" className="btn btn-primary" onClick={toggleSidebar}>
                                <i className="fa fa-bars"></i>
                                <span className="sr-only">Toggle Menu</span>
                            </button>
                        </div> */}
                        <div className="p-4 pt-5">
                            <h2 className='h2 fw-bolder'>
                                <Link to="/" className="logo text-white d-block text-decoration-none mb-3 fw-bolder">Note Taking</Link>
                            </h2>
                            <ul className="list-unstyled components mb-5">
                                <li className="nav-item">
                                    <Link to="/" className={`nav-item  pointer fs-5 text-decoration-none ${location.pathname === '/' && Style.active}`} aria-current="page">
                                        Home
                                    </Link>
                                </li>
                                <li>
                                    <span onClick={logOut} className={`nav-item pointer fs-5`}>
                                        Logout
                                    </span>
                                </li>
                            </ul>
                            <div className="footer">
                                <p>
                                    Copyright &copy; All rights reserved | This template is made with <i className="icon-heart" aria-hidden="true"></i> by Doaa Mahmoud
                                </p>
                            </div>
                        </div>
                    </nav>
                </div>
            ) : ''}
        </>
    );
}

export default SideBar;
